export type ITokenInfoItem = {
    token: string,
    detail: {
        price: string,
        symbol: string,
        updatedAt: number
    }
}

const AssetPanel = withTranslation('common')(({t,...rest}: WithTranslation) => {
    const container = useRef(null);
    const [pageSize, setPageSize] = useState(10);
    
    const [chartPeriod, setChartPeriod] = useState('24 H')

    // const { accountId,apiKey } = store.getState().account;

    // useEffect(() => {
    //     (async function getUserApi () {
    //         if (LoopringAPI && LoopringAPI.userApi && accountId && apiKey) {
    //             const { userBalances } = await LoopringAPI.userApi.getUserBalances({
    //                 accountId,
    //                 tokens: '' // get all the tokens
    //             }, apiKey)
    //             console.log(userBalances)
    //             // const { userBalances } = userBalances
    //         }
    //     })()
    // }, [accountId, apiKey])

    useEffect(() => {
        // @ts-ignore
        let height = container?.current?.offsetHeight;
        if (height) {
            setPageSize(Math.floor((height - 120) / 44) - 1);
        }
    }, [container, pageSize]);

    const {
        ShowDeposit,
        ShowTransfer,
        ShowWithdraw,
        ShowResetAccount,
    } = useModals()

    const onShowDeposit = () => {
        ShowDeposit(true)
    }

    const onShowTransfer = () => {
        ShowTransfer(true)
    }

    const onShowWithdraw = () => {
        ShowWithdraw(true)
    }

    const AssetTitleProps: AssetTitleProps = {
        assetInfo: {
            totalAsset: 123456.789,
            priceTag: PriceTag.Dollar,
        },
        onShowDeposit,
        onShowTransfer,
        onShowWithdraw,
    }

    const { tokens: tokensAll } = useGetTokens()

    const tokens = tokensAll?.tokenSymbolMap

    // const { balances } = useGetUserBalances(tokens)

    const { walletLayer2 } = store.getState().walletLayer2;
    const { faitPrices } = store.getState().system
    const tokenPriceList = Object.entries(faitPrices).map(o => ({
        token: o[0],
        detail: o[1]
    })) as ITokenInfoItem[]

    const tokenMap = Object.entries(store.getState().tokenMap.tokenMap || {}).map(o => ({
        token: o[0],
        tokenDetail: o[1]
    }))
    const coinList = walletLayer2 ? Object.keys(walletLayer2) : []
    const coinDetailList = walletLayer2 ? Object.values(walletLayer2) as any : []

    const doughnutData = coinList.map((coin, index) => ({
        name: coin,
        value: Number(coinDetailList[index].total)
    }))

    // TODO: cannot get lp token or smallbalances for now
    const assetsRawData = coinList.map((coin, index) => {
        const tokenInfo = coinDetailList[index]
        const tokenPriceUSDT = (Number(tokenPriceList.find(o => o.token === coin)?.detail.price) / Number(tokenPriceList.find(o => o.token === 'USDT')?.detail.price))
        
        return ({
            token: {
                type: tokenMap.find(o => o.token === coin)?.tokenDetail.isLpToken ? TokenType.lp : TokenType.single,
                value: coin
            },
            amount: tokenInfo.total,
            available: Number(tokenInfo.total) - Number(tokenInfo.locked),
            locked: Number(tokenInfo.locked),
            smallBalance: tokenPriceUSDT * tokenInfo.total < 1,
        })
    })

    return (
        <>
            <Box>
                <AssetTitle  {...{
                    t,
                    ...rest,
                    ...AssetTitleProps
                }} />
            </Box>

            {/*<div className="title">{t('labelAssetsTitle')}</div>*/}

            <StyledChartWrapper display={'flex'} justifyContent={'space-between'} alignItems={'center'} marginTop={2}>
                <Paper component={'div'}>
                    <Typography component="span" color="textSecondary" variant="body1">Asset Distribution</Typography>
                    <DoughnutChart data={doughnutData} />
                </Paper>
                <Paper component={'div'}>
                    <Typography component="span" color="textSecondary" variant="body1">Total Assets</Typography>
                    <ScaleAreaChart type={ChartType.Trend} data={testTrendData} />
                    <StyledBtnGroupWrapper>
                        <ToggleButtonGroup exclusive size="small" {...{...rest, t, data: toggleData, value: chartPeriod, setValue: setChartPeriod}} />
                    </StyledBtnGroupWrapper>
                </Paper>
            </StyledChartWrapper>
            <StylePaper marginTop={2}>
                <div className="tableWrapper" ref={container}>
                    <AssetsTable {...{
                        rawData: assetsRawData,
                        pagination: {
                            pageSize: pageSize
                        },
                        showFiliter: true
                    }} />
                </div>
            </StylePaper>
        </>
    )
})

export default AssetPanel
