    // const {coinMap} = useTokenMap();
    // const ammMapState = useAmmMap();
    // const walletLayer2State = useWalletLayer2();
    const [ammActivityViewMap, setAmmActivityViewMap] = React.useState<Array<AmmCardProps<I>>>([]);


